import { useEffect, useRef, useState } from "react";
import { AiOutlineFileImage } from "@react-icons/all-files/ai/AiOutlineFileImage";
import { FaDog } from "@react-icons/all-files/fa/FaDog";
import useAuthSession from "utils/useAuthSession";
import PrimaryButton from "components/PrimaryButton/PrimaryButton";
import BtnInputFile from "components/BtnInputFile/BtnInputFile";
import SimpleGallery from "components/SimpleGallery/SimpleGallery";
import ToggleButton from "components/ToggleButton/ToggleButton";
import "./PetPostNew.css";

const PetPostNew = () => {
    const textareaRef = useRef(null);
    const [data, setData] = useState({});
    const [files, setFiles] = useState([]);
    const [images, setImages] = useState([]);
    const { sessionData } = useAuthSession();
    const limitContent = 150;

    useEffect(() => {
        textareaRef.current.style.height = "0px";
        const scrollHeight = textareaRef.current.scrollHeight;
        textareaRef.current.style.height = scrollHeight + "px";
    }, [data]);


    const handleChange = (prop, value) => {
        setData(data => {
            let newData = { ...data };
            newData[prop] = value;
            return newData;
        });
    };

    const handleChangeImage = (evt) => {
        const newImage = evt.target.files[0];

        setFiles(files => ([...files, newImage,]));

        var reader = new FileReader();

        reader.onload = function (e) {
            setImages(images => ([
                ...images,
                e.target.result,
            ]));
        };

        reader.readAsDataURL(newImage);
    };

    const onRemoveImage = (index, image) => {
        setFiles(files.filter((_, i) => i !== index));
        setImages(images.filter((_, i) => i !== index));
    };

    const handleCreatePost = () => {
        var formData = new FormData();
        Object.keys(data).forEach(key => formData.append(key, data[key]));
        files.forEach((file, index) => formData.append('image' + index, file));
        for (let i = 0; i < 4; i++) {
            if (formData.get('image' + i) == null)
                formData.append('image' + i, '');
        }

        const requestOptions = {
            method: 'POST',
            headers: {
                'Authorization': sessionData.token,
            },
            body: formData,
        };

        fetch('/post/new', requestOptions)
            .then(response => response.json())
            .then(data => {
                console.log(data);
            });
    };

    const styleCounterChars = "counter-chars " + (data.content?.length >= limitContent ? "limit" : "");

    return (
        <div className="petpost-new">
            <div className="content">
                <textarea ref={textareaRef}
                    placeholder="¿Qué estas pensando?"
                    onChange={e => handleChange("content", e.target.value)}
                    maxLength={limitContent}
                ></textarea>

                {images.length > 0 && <SimpleGallery images={images} onRemove={onRemoveImage} />}
            </div>
            <div className="footer">
                <div className="options">
                    <BtnInputFile accept="image/*"
                        disable={images.length === 4}
                        onChange={handleChangeImage}>
                        <AiOutlineFileImage size="2em" />
                    </BtnInputFile>

                    <ToggleButton onChange={selected => handleChange("adopt", selected)}>
                        <FaDog size="2em" />
                        {data.adopt && <label>En adopción</label>}
                    </ToggleButton>
                </div>
                <div className="separator"></div>
                <div>
                    {data.content?.length > 0 && <label className={styleCounterChars}>{data.content?.length + '/' + limitContent}</label>}

                    <PrimaryButton onClick={handleCreatePost} disable={!data.content?.trim().length || !images.length}>Publicar</PrimaryButton>
                </div>
            </div>
        </div >
    );
};

export default PetPostNew;