import { useWindowSize, windowsMediaQueries } from "utils/useWindowSize";
import "./NavbarItem.css";

const NavbarItem = ({ text, icon, onClick, children }) => {
    const { width } = useWindowSize();

    return (
        <div className="navbar-item" onClick={onClick}>
            {icon}
            {width > windowsMediaQueries.Sm && <label>{text}</label>}
            {children}
        </div>
    );
};

export default NavbarItem;