import { useEffect, useState } from "react";
import useAuthSession from "utils/useAuthSession";
import PetPostHeader from "./PetPostHeader/PetPostHeader";
import PetPostOptions from "./PetPostOptions/PetPostOptions";
import "./PetPost.css";
import PetPostPhotos from "./PetPostPhotos/PetPostPhotos";

const PetPost = ({ post, onRemove }) => {
    const { sessionData } = useAuthSession();
    const [photos, setPhotos] = useState([]);

    useEffect(() => {
        const requestOptions = {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': sessionData.token,
            },
        };

        fetch('/post-image/' + post.id, requestOptions)
            .then(response => response.json())
            .then(data => setPhotos(data.info || []));
    }, []);

    return (
        <div className="petpost">
            <PetPostHeader post={post} onRemove={onRemove} />

            <div className="petpost-body">
                <p>{post.content}</p>

                <PetPostPhotos photos={photos} />
            </div>

            <PetPostOptions post={post} />
        </div>
    );
};

export default PetPost;