import React, { useRef } from "react";

import appConst from "const/AppConst";
import useAuthSession from "utils/useAuthSession";

import Brand from "./Brand/Brand";
import NavbarItem from "./NavbarItem/NavbarItem";
import DropDown from "components/DropDown/DropDown";
import DropDownItem from "components/DropDown/DropDownItem";
import "./NavBar.css";

const NavBar = ({ history, setSessionData }) => {
    const { sessionData } = useAuthSession();
    const avatarRef = useRef({});
    // const notifyRef = useRef({});

    const handleSignIn = () => {
        history?.push('/signup');
    };

    const closeSession = () => {
        setSessionData();
        history?.replace('/', '');
    };

    const logout = () => {
        const requestOptions = {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': sessionData.token,
            },
        };

        fetch('/user/logout', requestOptions)
            .then(response => response.json())
            .then(data => {
                if (data.success) closeSession();
            })
            .catch(() => closeSession());
    };

    return (
        <div className="navbar">
            <Brand history={history} brandName={appConst.name} />

            <div className="navbar-items">
                {
                    sessionData &&
                    <>
                        {/* <NavbarItem text="Notificaciones" icon="notification" onClick={e => notifyRef.current.toggle()}>
                            <DropDown ref={notifyRef}>
                                <DropDownItem text="Notificación 1" onClick={handleDropDownItem} />
                                <DropDownItem text="Notificación 2" onClick={handleDropDownItem} />
                            </DropDown>
                        </NavbarItem> */}

                        <NavbarItem text={sessionData.user.name} icon={<img alt={sessionData.user.name} src={sessionData.user.photo} className="avatar" />} onClick={e => avatarRef.current.toggle()}>

                            <DropDown ref={avatarRef}>
                                <DropDownItem text="Perfil" onClick={e => history?.push('/profile')} />
                                <DropDownItem text="Cerrar sesión" onClick={logout} />
                            </DropDown>
                        </NavbarItem>
                    </>
                }
                {
                    !sessionData &&
                    <>
                        {history?.location.pathname !== '/signup' && <NavbarItem text="Registrate" onClick={handleSignIn} />}
                    </>
                }
            </div>
        </div>
    );
};

export default NavBar;