import { useWindowSize, windowsMediaQueries } from "utils/useWindowSize";
import "./BannerWelcome.css";

const BannerWelcome = ({ title, description, urlImage, buttons }) => {
    const { width } = useWindowSize();

    const bannerStyle = width <= windowsMediaQueries.Sm ? "banner banner-mobile" : "banner";

    return (
        <div className={bannerStyle}>
            <div className="slogans">
                <div className="slogans-center">
                    <h1>{title}</h1>
                    <p>{description}</p>

                    <div className="slogans-buttons">
                        {buttons}
                    </div>
                </div>
            </div>
            <div className="container-image">
                <img src={urlImage} alt={title} />
            </div>
        </div>
    );
};

export default BannerWelcome;