import { useEffect, useState } from "react";

import { useApi } from "utils/useApi";
import useAuthSession from "utils/useAuthSession";

import InputText from "components/InputText/InputText";
import PrimaryButton from "components/PrimaryButton/PrimaryButton";
import ProfileImage from "components/ProfileImage/ProfileImage";

const Profile = () => {
    const { get, post } = useApi();
    const { sessionData } = useAuthSession();
    const [data, setData] = useState({ id: sessionData?.user?.id });
    const [error, setError] = useState(null);

    useEffect(() => {
        get('/profile/' + data.id).then(res => {
            console.log(res);
            if (res.success)
                setData({
                    id: data.id,
                    ...res.info,
                });
        });
    }, []);

    const handleChange = (prop, value) => {
        data[prop] = value
        setData(data);
    };

    const update = () => {
        setError(null);

        post('/profile/update', data)
            .then(res => {
                if (!res.success)
                    return setError(res.message);
            })
            .catch(err => setError(err));
    };

    return (
        <div>
            profile

            <hr />

            <InputText label="Nombres" value={data.firstName} onChange={e => handleChange("firstName", e.target.value)} />

            <InputText label="Apellidos" value={data.lastName} onChange={e => handleChange("lastName", e.target.value)} />

            <PrimaryButton block={true} onClick={update}>Guardar</PrimaryButton>


            <ProfileImage />
        </div>
    );
};

export default Profile;