import React, { useEffect, useState } from 'react';
import { Route, BrowserRouter as Router, Switch, Redirect } from 'react-router-dom';

import useAuthSession from 'utils/useAuthSession';
import { useApi } from 'utils/useApi';

import PageLoader from 'components/PageLoader/PageLoader';
import WelcomePage from 'pages/Welcome/WelcomePage';
import HomePage from 'pages/Home/HomePage';
import LoginPage from 'pages/Login/LoginPage';
import SignupPage from 'pages/Signup/SignupPage';

function App(props) {
    const { get } = useApi();
    const { sessionData, setSessionData } = useAuthSession();
    const [loading, setLoading] = useState(sessionData?.token ? true : false);

    const validateSession = () => {
        if (!sessionData) return;

        get('token/validate')
            .then(res => {
                if (!res.success)
                    return setSessionData(null);

                if (res.info) {
                    sessionData.token = res.info
                    setSessionData(sessionData);
                }
                
                setLoading(false);
            })
            .catch(() => {
                setSessionData(null);
                setLoading(false);
            });
    };

    useEffect(() => validateSession(), []);

    if (loading)
        return <PageLoader />;

    if (!sessionData) {
        return (
            <Router>
                <Switch>
                    <Route path='/' exact={true} component={WelcomePage} />
                    <Route path='/login' render={props => <LoginPage history={props.history} setSessionData={setSessionData} />} />
                    <Route path='/signup' component={SignupPage} />
                    <Redirect to="/login" />
                </Switch>
            </Router>
        );
    }

    return (
        <Router>
            <Switch>
                <Route path='/' render={props => <HomePage history={props.history} setSessionData={setSessionData} />} />
            </Switch>
        </Router>
    );
}

export default App;
