import useAuthSession from "./useAuthSession";


const useApi = () => {
    const { sessionData } = useAuthSession();

    const analyzeResponse = (url, requestOptions) => {
        if (sessionData)
            requestOptions.headers['Authorization'] = sessionData.token;

        return new Promise(async (resolve, reject) => {
            const res = await fetch(url, requestOptions);
            const statusRead = await res.body.getReader().read();
            const contentStr = new TextDecoder().decode(statusRead.value);

            const contentType = res.headers.get('Content-Type');
            const resContent = contentType?.indexOf('application/json') > -1 ? JSON.parse(contentStr) : contentStr;

            switch (res.status) {
                case 401:
                    // mandar al login
                    reject(resContent);
                    break;
                case 200:
                    resolve(resContent);
                    break;
                default:
                    reject(resContent);
            }
        });
    };

    const get = (url) => {
        const requestOptions = {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json'
            },
        };

        return analyzeResponse(url, requestOptions);
    };

    const post = (url, data, type) => {
        const requestOptions = {
            method: 'POST',
            headers: type == 'form-data' ? {} : {
                'Content-Type': 'application/json'
            },
            body: type == 'form-data' ? data : JSON.stringify(data)
        };

        return analyzeResponse(url, requestOptions);
    };

    const put = (url, data) => {
        const requestOptions = {
            method: 'PUT',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(data)
        };

        return analyzeResponse(url, requestOptions);
    };

    const remove = (url, data) => {
        const requestOptions = {
            method: 'DELETE',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(data)
        };

        return analyzeResponse(url, requestOptions);
    };


    return {
        get,
        post,
        put,
        remove,
    };
};


export { useApi };