import React, { useImperativeHandle, useState } from "react";
import { useOuterClick } from "utils/useOuterClick";
import "./DropDown.css";

const DropDown = ({ children, }, ref) => {
    const [open, setOpen] = useState(false);
    const innerRef = useOuterClick(ev => {
        open && setOpen(false)
    });

    const className = open ? "dropdown" : "hidden";
    const style = {
        top: (innerRef?.current?.parentNode?.offsetTop + innerRef?.current?.parentNode?.offsetHeight) + "px"
    };

    const newChildren = React.Children.map(children, child => {
        if (React.isValidElement(child)) {
            return React.cloneElement(child, { hide: () => setOpen(false) });
        }
        return child;
    });

    useImperativeHandle(ref, () => ({
        open: () => setOpen(true),
        close: () => setOpen(false),
        toggle: () => setOpen(!open),
    }));

    return (
        <div ref={innerRef} className={className} style={style}>
            <ul>
                {newChildren}
            </ul>
        </div>
    );
};

export default React.forwardRef(DropDown);