import { useState } from "react";
import Dots from "./Dots";
import "./PetPostPhotos.css";

const PetPostPhotos = ({ photos }) => {
    const [index, setIndex] = useState(0);
    const style = {
        backgroundImage: photos[index] ? `url(${photos[index].publicUrl})` : "",
    };

    const handlClick = (newIndex) => {
        setIndex(newIndex);
    };

    return (
        <div className="petpost-photos">
            <div className="wrapper">
                <div className="photo" style={style}></div>
            </div>

            {photos.length > 1 && <Dots length={photos.length} indexSelected={index} onClick={handlClick} />}
        </div>
    );
};

export default PetPostPhotos;