import { useEffect, useState } from "react";
import { Route, Switch, useRouteMatch } from "react-router";

import { useApi } from "utils/useApi";
import { routingUtils } from "utils/routingUtils";

import PetPostNew from "components/PetPostNew/PetPostNew";
import PetPost from "components/PetPost/PetPost";
import Profile from "pages/Home/Profile/Profile";
import EmptyListPost from "components/EmptyListPost/EmptyListPost";
import "./MainContent.css";

const MainContent = () => {
    let { path } = useRouteMatch();
    const [posts, setPosts] = useState([]);
    const [loading, setLoading] = useState(true);
    const { get } = useApi();
    const { combinePaths } = routingUtils();

    useEffect(() => {
        get('/post')
            .then(res => setPosts(res.info || []))
            .finally(() => setLoading(false));
    }, []);

    const onRemovePost = (post) => {
        setPosts(posts.filter(a => a.id != post.id));
    };

    return (
        <div className="main-content">
            <div className="padding">
                <Switch>
                    <Route path={path} exact>
                        <PetPostNew />

                        {!loading && !posts.length && <EmptyListPost />}

                        {posts.map((post) => (
                            <PetPost key={post.id} post={post} onRemove={onRemovePost} />
                        ))}
                    </Route>
                    <Route path={combinePaths(path, '/profile')}>
                        <Profile />
                    </Route>
                </Switch>
            </div>
        </div>
    );
};

export default MainContent;