const Dots = ({ length, indexSelected, onClick }) => {
    const dots = Array(length).fill(0);

    const getDot = (index) => {
        const style = "dot " + (index === indexSelected ? "selected" : "");
        return (
            <div key={index} onClick={e => onClick(index)} className={style}></div>
        );
    };

    return (
        <div className="dots">
            {dots.map((_, index) => getDot(index))}
        </div>
    );
};

export default Dots;