import NavBar from "components/NavBar/NavBar";
import BannerWelcome from "./BannerWelcome/BannerWelcome";

import appConst from "const/AppConst";
import imgLoveDos from "assets/images/undraw_good_doggy.svg";

import "./WelcomePage.css";
import PrimaryButton from "components/PrimaryButton/PrimaryButton";

const WelcomePage = (props) => {
    const description = `El lugar indicado para los amantes de las mascotas,\ningresa y ayuda a mejorar la calidad de vida de nuestras peludos amigos.`;

    const navigateLogin = () => {
        props.history.push('/login');
    };

    const buttonsBanner = (
        <>
            <PrimaryButton onClick={navigateLogin}>Ingresar</PrimaryButton>
        </>
    );

    return (
        <div className="welcome-page">
            <NavBar history={props.history} />

            <div className="body">
                <BannerWelcome
                    title={`¡Bienvenido a ${appConst.name}!`}
                    description={description}
                    urlImage={imgLoveDos}
                    buttons={buttonsBanner} />
            </div>
        </div>
    );
};

export default WelcomePage;