import { useRef } from "react";
import "./BtnInputFile.css";

const BtnInputFile = ({ accept, children, onChange, disable }) => {
    const inputRef = useRef(null);
    const style = "btn-input-file " + (disable ? "disabled" : "");

    const handleChange = (evt) => {
        onChange && onChange(evt);
        inputRef.current.value = null;
    };

    return (
        <label className={style}>
            {children}
            {!disable && <input ref={inputRef} type="file" accept={accept} onChange={handleChange} />}
        </label>
    );
};

export default BtnInputFile;