import BrandLogo from "./BrandLogo";
import "./Brand.css";

const Brand = ({ history, brandName }) => {
    const handleClick = () => {
        console.log(history);
        history?.push('/');
    };

    return (
        <div className="brand" onClick={handleClick}>
            <BrandLogo />
            <h4>{brandName}</h4>
        </div>
    );
};

export default Brand;