import "./SimpleGallery.css";
import SimpleGalleryImage from "./SimpleGalleryImage/SimpleGalleryImage";

const SimpleGallery = ({ images, onRemove }) => {
    const style = "simple-gallery div-" + images.length;
    return (
        <div className={style}>
            {images.map((image, index) => (
                <SimpleGalleryImage key={index} index={index} image={image} onRemove={onRemove} />
            ))}
        </div>
    )
};

export default SimpleGallery;