const routingUtils = () => {
    const combinePaths = (...paths) => {
        let finalPath = paths[0] || "";

        for (let i = 1; i < paths.length; i++) {
            if (finalPath[finalPath.length - 1] === "/")
                finalPath = finalPath.substr(0, finalPath.length - 1);

            if (paths[i][0] === "/")
                paths[i] = paths[i].substr(1);

            finalPath = finalPath + '/' + paths[i];
        }

        return finalPath;
    };

    return {
        combinePaths
    };
};

export { routingUtils };