import { useRef } from "react";
import { FiMoreHorizontal } from "@react-icons/all-files/fi/FiMoreHorizontal";
import DropDown from "components/DropDown/DropDown";
import DropDownItem from "components/DropDown/DropDownItem";
import PetPostButton from "components/PetPost/PetPostButton/PetPostButton";
import "./PetPostHeader.css";

const PetPostHeader = ({ post, onRemove }) => {
    const avatarRef = useRef({});

    const handleDeletePost = () => {
        onRemove && onRemove(post);
    };

    return (
        <div className="petpost-header">
            <img alt="user" src={post.userphoto} className="user-avatar" />

            <div className="petpost-text">
                <label>{post.username}</label>
                <small>El 31/02/2021</small>
            </div>

            <div className="petpost-more">
                <PetPostButton text={post.comments} onClick={e => avatarRef.current.toggle()}>
                    <FiMoreHorizontal size="2em" />

                    <DropDown ref={avatarRef}>
                        <DropDownItem text="Eliminar" onClick={handleDeletePost} />
                    </DropDown>
                </PetPostButton>
            </div>
        </div>
    );
};

export default PetPostHeader;