import { useState } from "react";
import { Link } from "react-router-dom";

import { useWindowSize, windowsMediaQueries } from "utils/useWindowSize";
import { useApi } from "utils/useApi";

import InputText from "components/InputText/InputText";
import NavBar from "components/NavBar/NavBar";
import PrimaryButton from "components/PrimaryButton/PrimaryButton";
import SimpleError from "components/SimpleError/SimpleError";
import "./LoginPage.css";

const LoginPage = ({ history, setSessionData }) => {
    const { width } = useWindowSize();
    const { post } = useApi();

    const styleCenterWrapper = "center-wrapper " + windowsMediaQueries.style(width);
    const [data, setData] = useState({});
    const [error, setError] = useState(null);

    const handleChange = (prop, value) => {
        data[prop] = value
        setData(data);
    };

    const login = () => {
        setError(null);

        post('/user/login', data)
            .then(res => {
                if (!res.success)
                    return setError(res.message);

                setSessionData({
                    token: res.info.jwt,
                    user: res.info.user,
                });

                history.replace('/', '');
            })
            .catch(err => setError(err));
    };

    return (
        <div className="login-page">
            <NavBar history={history} />

            <div className="body">
                <div className={styleCenterWrapper}>
                    <div className="card card-login">
                        <h2> Bienvenido </h2>

                        <SimpleError error={error} />

                        <InputText label="Usuario" onChange={e => handleChange("name", e.target.value)} />

                        <InputText label="Contraseña" type="password" onChange={e => handleChange("password", e.target.value)} />

                        <div className="buttons">
                            <PrimaryButton block={true} onClick={login}>Iniciar Sesión</PrimaryButton>
                        </div>

                        <div>
                            <small>¿Olvidaste tu contraseña?</small>
                        </div>
                    </div>

                    <div className="card card-singup">
                        <small>
                            ¿No tienes una cuenta? <Link to="signup">Registrate</Link>
                        </small>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default LoginPage;

