import { AiOutlineHeart } from "@react-icons/all-files/ai/AiOutlineHeart";
import { AiOutlineComment } from "@react-icons/all-files/ai/AiOutlineComment";
import { FaDog } from "@react-icons/all-files/fa/FaDog";
import PetPostButton from "components/PetPost/PetPostButton/PetPostButton";
import "./PetPostOptions.css";

const PetPostOptions = ({ post }) => {
    return (
        <div className="petpost-options">
            <PetPostButton text={post.likes}>
                <AiOutlineHeart size="2em" />
            </PetPostButton>

            <PetPostButton text={post.comments}>
                <AiOutlineComment size="2em" />
            </PetPostButton>

            <div className="option-adoption">
                <PetPostButton text={post.likes} primary={true}>
                    <FaDog size="2em" />
                    <label>Adoptar</label>
                </PetPostButton>
            </div>
        </div>
    );
};

export default PetPostOptions;
