import { useEffect, useState } from "react";
import "./InputText.css";

const InputText = ({ label, type, value, onChange }) => {
    const [focus, setFocus] = useState(false);
    const [style, setStyle] = useState(focus ? "input-text focus" : "input-text")

    const handleChange = (evt) => {
        evalValue(evt.target.value);
        if (onChange) onChange(evt);
    };

    const evalValue = (value) => {
        if ((value || "").length === 0)
            setStyle(style.replace(" focus-visible", ""));
        else if (style.indexOf("focus-visible") === -1)
            setStyle(style + " focus-visible");
    };

    useEffect(() => evalValue(value), [value]);

    return (
        <div className={style}>
            <label>
                <span>{label}</span>
                <input type={type || 'text'}
                    defaultValue={value}
                    onFocus={() => setFocus(true)}
                    onBlur={() => setFocus(false)}
                    onChange={e => handleChange(e)} />
            </label>
        </div>
    );
};

export default InputText;