import PrimaryButton from "components/PrimaryButton/PrimaryButton";
import { useState } from "react";
import { useApi } from "utils/useApi";
import useAuthSession from "utils/useAuthSession";

import "./ProfileImage.css";

const ProfileImage = () => {
    const { post } = useApi();
    const { sessionData, setSessionData } = useAuthSession();
    const [data, setData] = useState({});
    const [image, setImage] = useState(null);

    const style = {
        'backgroundImage': `url(${image})`
    };

    const handleChange = (evt) => {
        const newImage = evt.target.files[0];

        setData({
            image: newImage,
            ...data,
        });

        var reader = new FileReader();

        reader.onload = function (e) {
            setImage(e.target.result);
        };

        reader.readAsDataURL(newImage);
    };

    const updateImage = () => {
        console.log(data);
        const formData = new FormData();
        Object.keys(data).forEach(key => formData.append(key, data[key]));
        post('/profile/update-image/' + sessionData?.user?.id, formData, 'form-data')
            .then(res => {
                if (!res.success)
                    console.log(res);

                sessionData.user.photo = res.info;
                setSessionData(sessionData);
            })
    };

    return (
        <div>
            <input type="file" onChange={e => handleChange(e)} />

            <div className="img" style={style}></div>

            <PrimaryButton block={true} onClick={updateImage}>Guardar</PrimaryButton>
        </div>
    );
};

export default ProfileImage;