import "./PetPostButton.css";

const PetPostButton = ({ children, text, primary, onClick }) => {
    const style = "petpost-button " + (primary ? "primary" : "");

    return (
        <button className={style} onClick={onClick}>
            {children}
            {text && <label>{text}</label>}
        </button>
    );
};

export default PetPostButton;