import "./PrimaryButton.css";

const PrimaryButton = ({ children, onClick, block, disable }) => {
    const style = ("primary-button " + (block ? "block" : "") + " " + (disable ? "disabled" : "")).trim();

    return (
        <button className={style} onClick={onClick} disabled={disable}>
            {children}
        </button>
    );
};

export default PrimaryButton;