import { useState } from "react";
import { Link } from "react-router-dom";

import { useWindowSize, windowsMediaQueries } from "utils/useWindowSize";
import { useApi } from "utils/useApi";

import InputText from "components/InputText/InputText";
import NavBar from "components/NavBar/NavBar";
import PrimaryButton from "components/PrimaryButton/PrimaryButton";
import SimpleError from "components/SimpleError/SimpleError";
import "./SignupPage.css";

const SignupPage = ({ history }) => {
    const { width } = useWindowSize();
    const styleCenterWrapper = "center-wrapper " + windowsMediaQueries.style(width);
    const { post } = useApi();
    const [data, setData] = useState({});
    const [error, setError] = useState(null);

    const handleChange = (prop, value) => {
        data[prop] = value
        setData(data);
    };

    const signup = () => {
        post('/user/register', data)
            .then(res => {
                if (!res.success)
                    return setError(res.message);

                history?.push('/login');
            })
            .catch(err => setError(err));
    };

    return (
        <div className="login-page">
            <NavBar history={history} />
            <div className="body">
                <div className={styleCenterWrapper}>
                    <div className="card card-login">
                        <h2> Bienvenido </h2>

                        <SimpleError error={error} />

                        <InputText label="Correo electrónico" value={data.email} onChange={e => handleChange("email", e.target.value)} />

                        <InputText label="Usuario" value={data.name} onChange={e => handleChange("name", e.target.value)} />

                        <InputText label="Contraseña" type="password" value={data.password} onChange={e => handleChange("password", e.target.value)} />

                        <InputText label="Confirmar" type="password" value={data.password_confirm} onChange={e => handleChange("password_confirm", e.target.value)} />

                        <div className="buttons">
                            <PrimaryButton block={true} onClick={signup}>Aceptar</PrimaryButton>
                        </div>
                    </div>

                    <div className="card card-singup">
                        <small>
                            ¿Tienes una cuenta? <Link to="login">Ingresar</Link>
                        </small>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default SignupPage;