import { AiOutlineClose } from "@react-icons/all-files/ai/AiOutlineClose";

const SimpleGalleryImage = ({ index, image, onRemove }) => {
    const style = {
        'backgroundImage': `url(${image})`
    };

    return (
        <div className="simple-gallery-image">
            <button onClick={e => onRemove(index, image)}>
                <AiOutlineClose size="1.5em" />
            </button>
            <div className="img" style={style}></div>
        </div>
    );
};

export default SimpleGalleryImage;